[id="hornetcomb-header"] {
    background: var(--hgob) url(../images/combBack.svg);
    background-size: contain;
    background-blend-mode: saturation;
    @media screen and (min-width: 850px) {
        padding: 6rem 10%;
    }
    h2 {
        text-align: center;
        @media screen and (min-width: 850px) {
            transform: translateY(-3rem);

        }
    }
    .hexwrapper {
        .hex-row {
            ul {
                margin: 0 auto;
                list-style: none;
                align-items: center;
                @media (min-width: 500px) {
                    display: flex;
                    flex-wrap: wrap; //                    align-content: space-evenly;
                    //                    justify-content: space-evenly;
                }
                &.hex-1 {
                    max-width: 275px;
                    li {
                        &.nav-item {
                            transform: unset !important;
                        }
                    }
                }
                &.hex-2 {
                    max-width: 525px;
                }
                &.hex-3 {
                    max-width: 775px;
                }
                &.hex-4 {
                    max-width: 900px;
                }
                &.hex-5 {
                    max-width: unset;
                    li {
                        &.nav-item {
                            @media screen and (min-width: 1140px) {
                                font-size: 1.1rem;
                            }
                        }
                    }
                }
                &.hex-6 {
                    li {
                        &.nav-item {
                            @media screen and (min-width: 1140px) {
                                font-size: 1rem;
                            }
                        }
                    }
                }
                li {
                    padding: 1rem 0;
                    position: relative;
                    display: flex;
                    &.nav-item {
                        @media (max-width: 500px) {
                            flex: 1 0 100%;
                        }
                        @media (min-width: 501px) {
                            flex: 1 0 auto;
                            min-width: min-content;
                            justify-content: center;
                            align-items: center;
                            padding: 1rem;
                        }
                        @media (min-width: 850px) {
                            padding: 0;
                            &:nth-child(even) {
                                transform: translateY(30%);
                            }
                            &:nth-child(odd) {
                                transform: translateY(-25%);
                            }
                        }
                        @media (min-width: 1000px) {
                            flex: 1 0 min-content;
                            padding: 0;
                            background: none;
                            &:nth-child(even) {
                                transform: translateY(25%);
                            }
                            &:nth-child(odd) {
                                transform: translateY(-20%);
                            }
                        }

                        a {
                            color: var(--ssgr);
                            hyphens: none;
                            padding: 2rem;
                            display: flex;
                            align-items: center;
                            text-decoration: none;
                            text-shadow: 4px 4px 0 var(hgob60a);
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            top: 0;
                            left: 0;
                            @media (min-width: 500px) {
                                justify-content: center;
                                text-align: center;
                            }
                            @media (min-width: 850px) {
                                padding: 2rem 10%;
                                font-size: initial;
                            }
                            @media screen and (min-width:1140px) {
                                font-size: 1.3rem;
                            }
                            &:hover,
                            &:focus {
                                color: var(--ssgr);
                                text-decoration: underline;
                                text-shadow: unset;
                            }
                        }
                        svg {
                            fill: var(--wgo60a);
                            &:hover,
                            &:focus {
                                fill: var(--wgo40a);
                            }
                            @media (min-width: 850px) {
                                height: 90%;
                                width: 90%; // position: static;
                            }
                        }

                        &:nth-of-type(1) svg,
                        &:nth-of-type(4n) svg {
                            fill: var(--wgo);
                            &:hover,
                            &:focus {
                                fill: var(--wgo40a);
                            }
                        }
                        &:nth-of-type(2) svg,
                        &:nth-of-type(5) svg {
                            fill: var(--wgolt);
                            &:hover,
                            &:focus {
                                fill: var(--wgo40a);
                            }
                        }
                        &:nth-of-type(3n) svg {
                            fill: var(--wgomd);
                            &:hover,
                            &:focus {
                                fill: var(--wgo40a);
                            }
                        }
                    }
                }
            }
        }
    }
}
